body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.app {
  min-width: 400px;
  min-height: 200px;
  padding: 20px;
  border: 1px solid lightgrey;
  border-radius: 10px;
  position: relative;
}

.tasksForm {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

input[type='checkbox'] {
  font-size: 20px;
  cursor: pointer;
}

input[type='text'] {
  border: 0;
  outline: none;
  background-color: whitesmoke;
  font-size: 15px;
}

button {
  border: 0;
  outline: none;
  background-color: whitesmoke;
  cursor: pointer;
  font-size: 14px;
}

.timesBtn {
  color: grey;
}

.menu {
  position: absolute;
  bottom: 10px;
  width: 100%;
}

